export default {
	props: [
		'liked_by',
		'comment_count',
	],
	computed: {
		like_count() {
			return this.split_string(this.liked_by).length;
		},
		post_liked() {
			return this.split_string(this.liked_by).includes(frappe.session.user);
		},
		liked_by_data() {
			return JSON.stringify(this.split_string(this.liked_by));
		}
	},
	methods: {
		split_string(str) {
			return str && str !== '' ? str.split('\n') : []
		}
	}
}
