export default {
	props: ['src'],
	data() {
		return {
			full_size: false
		}
	},
	created() {
		document.addEventListener('keyup', this.close_preview_on_escape);
	},
	destroyed() {
		document.removeEventListener('keyup', this.close_preview_on_escape);
	},
	methods: {
		close_preview_on_escape(e) {
			if (e.keyCode === 27) {
				this.$root.$emit('hide_preview')
			}
		}
	}
}
